<template>
  <div class="personal-content-body">
    <div class="content folder-content">
      <div class="content-title clear">
        <div class="left">{{typename}}文件管理</div>
        <el-input placeholder="输入文件关键字进行搜索" v-model="search" class="header-right-time right" :clearable='true' @change="initFolderList" >
          <i slot="prefix" class="el-input__icon el-icon-search"></i>
        </el-input>
        <div class="historyapply-group-top-dropdown select-normal right" style="margin-right:15px;" :class="{'select-normal-highlight':noprojectselect}" v-if="pagetype!=2">
          <el-select v-model="fileType.value" filterable placeholder="请选择" @change="changeProject($event)" :disabled='!showNumber'>
            <el-option v-for="item in fileType.filetypeList" :key="item.id" :label="item.name" :value="item.id">
            </el-option>
          </el-select>
        </div>
      </div>
      <div class="historyapply-group folder-historyapply-group">
        <div class="historyapply-group-top clear">
          <!-- <div class="breadbox left" v-if="pagetype==2">
            <span class="breadicon left">
              <i class="el-icon-d-arrow-left"></i>
            </span>
            <router-link v-if="pagefrom==2" class="breadtext left" :to="{ name: 'investProjectManageHomepage' ,query: {id:projectId}}">返回项目首页</router-link>
            <router-link v-if="pagefrom==3" class="breadtext left" :to="{ name: 'newProjectProcess' ,query: {id:projectId,cName:projectName}}">返回项目首页</router-link>
          </div> -->
          <div class="historyapply-group-top-title left" v-if="pagetype!=2&&showNumber">共包含{{total}}个{{typename}}</div>
          
          <div class="right">
            <div class="historyapply-group-top-btn right" @click="uploadFileFun">
              <span class="filemanage-icon icon-upload"></span>上传文件
            </div>
            <div class="historyapply-group-top-btn right" @click="createNewfileFun">
              <span class="filemanage-icon icon-create"></span>新建文件夹
            </div>
            <div class="historyapply-group-top-btn right" v-if="type==1" @click="gotoDocumentView">
              <span class="filemanage-icon icon-view"></span>文件总览
            </div>
          </div>
          <div class="clear"></div>
        </div>
        <div class="foldermanage-content">
          <!-- <div class="foldermanage-content-title">档案库</div> -->
          <div class="foldermanage-content-table clear">
            <div class="foldermanage-content-table-th clear">
              <div class="table-th1">类目</div>
              <div class="table-th2">文件数量</div>
              <div class="table-th3">修改时间</div>
            </div>
            <div class="foldermanage-content-table-line"></div>
            <!-- 已有文件夹 -->
            <div class="foldermanage-content-table-content">
              <div v-for="(item,index) in fileList" @click="gotoDocumentList(item.id,item.dirname,item.amend,item.companyId)">
                <div class="foldermanage-content-table-tr clear" v-if="item.id!=-1">
                  <div class="table-td1 clear">
                    <span class="filemanage-icon icon-folder left"></span>
                    <div class="filename-box left">
                      <div class="filename-box-text" v-if="!item.edited">{{item.dirname}}</div>
                      <input @click.stop="" class="filename-box-input" :class="{'filename-box-input-error':item.error!=''}" type="text" name="" v-model="item.dirname" v-if="item.edited" @focus.stop="hideError(index)" @blur="hideEdit(index)" maxlength="20">
                      <span class="filename-box-input-errortext">{{item.error}}</span>
                    </div>
                    <div class="filename-btnbox left" v-if="item.edited">
                      <span class="filemanage-icon icon-zq left" @click.stop="commitEditname(index)"></span>
                      <span class="filemanage-icon icon-cw left" @click.stop="cancelEditname(index)"></span>
                    </div>
                    <div class="filename-btnbox-right right" v-if="item.amend==1">
                      <span class="filemanage-icon icon-delete" @click.stop="deleteFileFunc(index)"></span>
                      <span class="filemanage-icon icon-edit" @click.stop="editNameFunc(index)"></span>
                    </div>
                  </div>
                  <div class="table-td2">
                    {{item.count}}个文件
                  </div>
                  <div class="table-td3">
                    {{item.edit_time}}
                  </div>
                </div>
                <div class="foldermanage-content-table-line"></div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- 文件上传弹窗 -->
      <el-dialog title="文件上传" :visible.sync="fileuploadDialog" width="900px" center :append-to-body='true' :before-close="dialogClose">
        <div class="dialog_content">
          <div class="form-group margin-bottom-none">
            <div class="form-group-inputboxgroup clear">
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>所属项目</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-select v-model="uploadfile.projectVal" filterable placeholder="请选择" @change="changeModalselect('projectId',$event);getFolderList()">
                    <el-option v-for="item in uploadfile.projectList" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
              <div class="form-group-inputbox">
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>文件分类</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-select v-model="uploadfile.folderListVal" filterable placeholder="请选择" @change="changeModalselect('folderListId',$event)">
                    <el-option v-for="item in uploadfile.folderList" :key="item.id" :label="item.dirname" :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
            </div>
            <div class="form-group-inputboxgroup clear margin-bottom-none">
              <div class="form-group-inputbox">
                {{fileListError}}
                <h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>所选文件</h2>
                <!-- <div class="form-group-inputbox-fileselectbox">
                <input class="form-group-inputbox-fileselectbox-inputfile" type="file" multiple="multiple" @change="fileListChange($event)">
                <i class="el-icon-plus"></i>添加文件
              </div> -->
                <!-- <input class="form-group-inputbox-inputtext" type="text"> -->
                <!-- <span class="form-group-inputbox-error">{{fileListError}}</span>
              <div class="form-group-selectedfile-box">
                <div class="form-group-selectedfile-item clear" v-for="(item,index) in uploadfile.fileList">
                  <span class="filemanage-icon icon-wd"></span>
                  <span class="form-group-selectedfile-filename">{{item.name}}</span>
                  <span class="filemanage-icon icon-warning" v-if="!item.typeCheck"></span>
                  <span class="el-icon-close" @click="deleteFile(index)"></span>
                </div>
              </div> -->
                <el-upload class="upload-demo" drag action="" multiple :file-list="uploadfile.fileList" :auto-upload="false" :show-file-list="false" :on-change="fileListChange">
                  <i class="el-icon-upload"></i>
                  <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
                  <div class="el-upload__tip" slot="tip">不能上传音频和视频文件,不支持文件夹上传</div>
                </el-upload>
                <div class="form-group-selectedfile-box">
                  <div class="form-group-selectedfile-item clear" v-for="(item,index) in uploadfile.fileList">
                    <span class="filemanage-icon icon-wd"></span>
                    <span class="form-group-selectedfile-filename">{{item.name}}</span>
                    <span class="filemanage-icon icon-warning" v-if="!item.typeCheck"></span>
                    <span class="el-icon-close" @click="deleteFile(index)"></span>
                  </div>
                </div>
              </div>
              <div class="form-group-inputbox" v-if="type==1">
                <h2 class="form-group-inputbox-title">标签选择</h2>
                <div class="form-group-inputbox-selectbox select-normal-40 left">
                  <el-select v-model="uploadfile.tagListVal" filterable placeholder="请选择" @change="changeModalselect('tagListId',$event)">
                    <el-option v-for="item in uploadfile.tagList" :key="item.id" :label="item.name" :value="item.id">
                    </el-option>
                  </el-select>
                </div>
                <span class="form-group-inputbox-error"></span>
              </div>
            </div>
          </div>
        </div>
        <div slot="footer" class="dialog_footer clear">
          <button class="dialog-btn-submit btn-blue" :class="{'btn-blue-disabled':!submitOk}" @click="submitClick()">上传</button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import { allProjectOrFund, projectOrFundDir, mkProjOrFundDir, editProjOrFundDir, delProjOrFundDir, getTags, archiveUploadFile, rootDirInfo } from '../../config/api'
import { dialogConfirm, dialogConfirmHasbtn, getFileTypeMutil } from '../../config/mUtils'
export default {
  data() {
    return {
      type: 1, //1项目 2基金
      pagetype: 1, //1=>1级 2=>2级
      pagefrom:1,//2=>来自项目管理3=>来自新项目管理
      typename: '项目', //1项目 2基金
      projectId: -1, //项目id，-1->为选择项目
      projectName: '', //项目id，-1->为选择项目
      noprojectselect: false,
      fileType: {
        value: '',
        filetypeList: [],
        placeholder: '请选择'
      },
      fileList: '',
      currentName: '', //修改之前的名字
      search: '', //搜索关键字
      /////////////////////////////////上传文件弹窗
      fileuploadDialog: false,
      uploadfile: {
        projectList: '',
        projectVal: '',
        projectId: '',
        folderList: '',
        folderListVal: '',
        folderListId: '',
        fileList: [],
        tagList: '',
        tagListVal: '',
        tagListId: '',
      },
      showNumber: true,
    }
  },
  watch: {
    $route(to, from) {
      this.initData();
    }
  },
  created() {
    this.initData();
  },
  computed: {
    total: function() {
      if ((this.fileType.filetypeList.length - 1) < 0) {
        return 0
      } else {
        return this.fileType.filetypeList.length - 1
      }
    },
    //弹出框验证
    projectIdError: function() {
      if (this.uploadfile.projectVal == '') {
        return true
      } else {
        return false
      }
    },
    folderIdError: function() {
      if (this.uploadfile.folderListVal == '') {
        return true
      } else {
        return false
      }
    },
    fileListError: function() {
      let count = 0;
      if (this.uploadfile.fileList.length > 0) {
        this.uploadfile.fileList.forEach(item => {
          if (!item.typeCheck) {
            count++;
          }
        })
      }
      if (count > 0) {
        return '暂不支持视频和音频文件'
      } else {
        return ''
      }
    },
    tagListError: function() {
      if (this.uploadfile.tagListVal == '') {
        return false
      } else {
        return false
      }
    },
    submitOk: function() {
      if (this.type == 1) {
        if (!this.projectIdError && !this.folderIdError && this.fileListError == '' && !this.tagListError && this.uploadfile.fileList.length > 0) {
          return true;
        } else {
          return false
        }
      } else {
        if (!this.projectIdError && !this.folderIdError && this.fileListError == '' && this.uploadfile.fileList.length > 0) {
          return true;
        } else {
          return false
        }
      }
    }
  },
  mounted() {},
  methods: {
    //初始化页面
    initData() {
      let routename = this.$route.name;
      if (routename == 'directInvestProjectRunProjectFile') {
        this.type = 1;
        this.typename = '项目';
      } else {
        this.type = 2;
        this.typename = '基金';
      }
      if (this.$route.query.projectId != undefined) {
        this.projectId = this.$route.query.projectId
      } else {
        this.projectId = -1
      }
      if (!this.$route.query.from||this.$route.query.from==1) {
        this.pagetype = 1;
        this.pagefrom=1;//没有来自哪里
      } else {
        this.pagetype = 2;
        this.pagefrom=this.$route.query.from;
        this.projectName=this.$route.query.cName;
      }
      if (this.$route.query.isNewProject != undefined) {
        this.showNumber = false
      }
      this.initProjectType();
      this.initFolderList();
    },
    //初始化项目列表
    initProjectType() {
      allProjectOrFund({
        type: this.type
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.fileType.filetypeList = res.data.payload.data;
          if (res.data.payload.data.length == 0) {
            this.fileType.placeholder = '暂无数据'
          } else {
            this.fileType.filetypeList.unshift({
              id: -1,
              name: '全部'
            });
            if (this.projectId == -1) {
              this.fileType.value = ''
            } else {
              this.fileType.filetypeList.forEach(item => {
                if (item.id == this.projectId) {
                  this.fileType.value = item.name
                }
              })
            }
          }
        }
      });
    },
    //前往文件总览
    gotoDocumentView() {
      let query=JSON.parse(JSON.stringify(this.$route.query));
      query.type=this.type;
      // console.log(query);
      this.$router.push({
        name: 'directInvestProjectRunProjectFileView',
        query: query
      })
    },
    //初始化文件列表
    initFolderList() {
      this.currentName='';
      let param = {
        type: this.type,
        search: this.search
      }
      if (this.projectId != -1) {
        param.id = this.projectId
      }
      rootDirInfo(param).then(res => {
        if (res.data.msgNo == 1) {
          let fl = res.data.payload.data;
          fl.forEach(item => {
            item.edited = false; //是否正在修改
            item.error = '';
            item.error_show = false;
          });
          fl.unshift({
            id: -1,
            dirname: '',
            add_time: '',
            error: '',
            count: 0,
            amend: 0,
            error_show: false
          })
          this.fileList = fl;
        }
      })
    },
    //切换项目
    changeProject(e) {
      //console.log(1111);
      //console.log(e);
      this.projectId = e;
      this.noprojectselect = false;
      this.initFolderList();
    },
    //点击创建新文件夹按钮
    createNewfileFun() {
      if (this.projectId == -1) {
        dialogConfirm('info', '您还没有选择' + this.typename);
        this.noprojectselect = true;
      } else {
        this.fileList[0].id = -2;
        this.fileList[0].dirname = '';
        this.fileList[0].edited = true;
        this.fileList[0].add_time = new Date().Format("yyyy-MM-dd hh:mm:ss");
      }
    },
    //点击修改文件夹按钮
    editNameFunc(index) {
      this.fileList.forEach((item, index) => {
        if (index == 0) {
          item.id = -1;
        } else {
          item.edited = false;
        }
      })
      this.fileList[index].edited = true;
      this.currentName = this.fileList[index].dirname
    },
    /////////提交修改/创建文件夹
    commitEditname(index) {
      let currentfile = this.fileList[index];
      //新增文件夹
      if (currentfile.dirname == '') {
        this.fileList[index].error = '*文件名不能为空';
        return;
      }
      if (index == 0) {
        mkProjOrFundDir({
          type: this.type,
          dirname: currentfile.dirname,
          id: this.projectId,
          pid: ''
        }).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '文件创建成功');
            this.initFolderList();
          } else {
            dialogConfirm('fail', res.data.message);
          }
        })
      } else {
        if (this.currentName == currentfile.dirname) {
          this.fileList[index].edited = false;
        } else {
          editProjOrFundDir({
            type: this.type,
            dirname: currentfile.dirname,
            dirId: currentfile.id
          }).then(res => {
            if (res.data.msgNo == 1) {
              dialogConfirm('success', '文件修改成功');
              this.initFolderList();
            }else{
              dialogConfirm('fail', res.data.message);
            }
          })
        }
      }
    },
    //取消创建新文件夹
    cancelEditname(index) {
      if (index == 0) {
        this.fileList[index].id = -1
      } else {
        this.fileList[index].edited = false;
        this.fileList[index].dirname = this.currentName;
      }
    },
    hideError(index) {
      this.fileList[index].error = '';
    },
    hideEdit(index) {
      if (this.fileList[index].dirname == this.currentName) {
        if (index == 0) {
          this.fileList[index].id = -1
        } else {
          this.fileList[index].edited = false;
        }
      }

    },
    //删除文件夹
    deleteFileFunc(index) {
      delProjOrFundDir({
        type: this.type,
        dirId: this.fileList[index].id,
        count: this.fileList[index].count
      }).then(res => {
        if (res.data.msgNo == 1) {
          dialogConfirm('success', '文件夹删除成功');
          this.initFolderList();
        } else {
          dialogConfirm('fail', res.data.message);
        }
      })
    },
    //前往文件列表，
    //id=>文件夹id 
    //name=>文件夹名称
    //companyId=>项目id
    //ammend=>0默认文件夹，1=>非默认文件夹
    gotoDocumentList(id, name, ammend, companyId) {
      let routername = '';
      if (ammend != 0) {
        this.projectId = companyId;
      }
      if (this.type == 1) {
        routername = 'directInvestProjectRunProjectFileDoc'
      } else {
        routername = 'fundDocumentManage'
      }
      if (id != -1 && id != -2) {
        this.$router.push({
          name: routername,
          query: {
            folderId: id,
            folderName: name,
            projectId: this.projectId,
            ammend: ammend,
            pname: this.projectName,
            from:this.pagefrom
          }
        })
      }
    },
    /*------------------------------------弹窗---------------------------------------------*/
    //获取项目列表
    getProjectList() {
      //获取项目列表
      allProjectOrFund({
        type: this.type
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.uploadfile.projectList = res.data.payload.data;
          if (this.projectId == -1) {
            this.uploadfile.projectVal = '';
            this.uploadfile.projectId = '';
          } else {
            this.uploadfile.projectId = this.projectId;
            this.uploadfile.projectList.forEach(item => {
              if (item.id == this.uploadfile.projectId) {
                this.uploadfile.projectVal = item.name
              }
            })
          }
          this.getFolderList();
        }
      });
    },
    //获取根目录文件夹列表
    getFolderList() {
      rootDirInfo({
        type: this.type,
        id: this.uploadfile.projectId
      }).then(res => {
        if (res.data.msgNo == 1) {
          this.uploadfile.folderList = res.data.payload.data;
          this.uploadfile.folderListVal = '';
          this.uploadfile.folderListId = this.uploadfile.folderList[0].id;
        }
      })
    },
    //获取标签列表
    getTagsList() {
      getTags().then(res => {
        if (res.data.msgNo == 1) {
          this.uploadfile.tagList = res.data.payload;
          this.uploadfile.tagListVal = ''
          this.uploadfile.tagListId = 0
        }
      })
    },
    //下拉选项变化
    changeModalselect(key, id) {
      this.uploadfile[key] = id;
    },
    //截取文件后缀名
    getFileType(filename) {
      let type = filename.substring(filename.lastIndexOf("."), filename.length);
      let unsupportType = ['.3gpp', '.ac3', '.mp2', '.mp3', '.mp4', '.mpeg', '.mpg', '.wma', '.aac'];
      if (unsupportType.indexOf(type) == -1) {
        return true
      } else {
        return false
      }
    },
    //选择文件
    /*fileListChange(e) {
      Array.from(e.target.files).forEach(item => {
        item.typeCheck = this.getFileType(item.name);
        this.uploadfile.fileList.push(item);
      })
      e.target.value = "";
    },*/
    fileListChange(file, filelist) {
      this.uploadfile.fileList = [];
      filelist.forEach(item => {
        let type = getFileTypeMutil(item.name);
        //console.log(type);
        //console.log(item.name);
        //console.log(item);
        item.typeCheck = this.getFileType(item.name);
        if (type == "") {
          dialogConfirm('info', '不支持上传此格式');
        } else {
          this.uploadfile.fileList.push(item);
        }
      })
      /*Array.from(e.target.files).forEach(item => {
        item.typeCheck = this.getFileType(item.name);
        this.uploadfile.fileList.push(item);
      })
      e.target.value = "";*/
    },
    //上传文件
    uploadFileFun() {
      // console.log(this.fileType.filetypeList.length);
      if (this.fileType.filetypeList.length != 1) {
        this.fileuploadDialog = true;
        this.uploadfile = {
          projectList: '',
          projectVal: '',
          projectId: '',
          folderList: '',
          folderListVal: '',
          folderListId: '',
          fileList: [],
          tagList: '',
          tagListVal: '',
          tagListId: '',
        }
        this.getProjectList();
        this.getTagsList();
      } else {
        dialogConfirm('info', '您还没有项目，请先创建项目');
      }

    },
    //删除文件
    deleteFile(index) {
      this.uploadfile.fileList.splice(index, 1);
    },
    //弹窗关闭确认
    dialogClose() {
      if (this.uploadfile.fileList.length > 0) {
        dialogConfirmHasbtn('确定不提交并关闭吗？').then(() => {
          this.fileuploadDialog = false;
        }).catch(() => {});
      } else {
        this.fileuploadDialog = false;
      }
    },
    //提交上传文件弹窗
    submitClick() {
      if (this.submitOk) {
        let param = {
          type: this.type,
          companyId: this.uploadfile.projectId,
          dirId: this.uploadfile.folderListId,

        };
        if (this.type == 1) {
          param.tagId = this.uploadfile.tagListId;
        } else {
          param.tagId = ''
        }
        let fl = [];
        this.uploadfile.fileList.forEach(item => {
          fl.push(item.raw);
        })
        archiveUploadFile(param, fl).then(res => {
          if (res.data.msgNo == 1) {
            dialogConfirm('success', '文件上传成功');
          } else {
            dialogConfirm('error', res.data.message);
          }
          this.fileuploadDialog = false;
          this.uploadfile.fileList = [];
          this.initFolderList();
        })
      } else {
        if (this.projectIdError || this.folderIdError || this.tagListError || this.uploadfile.fileList.length == 0) {
          dialogConfirm('info', '您还有必填项没有填写～')
        } else {
          dialogConfirm('info', '文件格式不正确')
        }
      }
    }
  }
}

</script>
<style lang="scss" scoped>
@import '../../style/modal.scss';
@import '../../style/folder.scss';

.foldermanage-content {
  border-top: none;
}

.breadcrumb {
  margin-top: 5px;
}

